import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

import { CommonProperties } from '../common-properties';
import { xhrHeaders } from '../xhr-headers';
import { Observable } from 'rxjs/Observable';
import { CommonService } from 'app/services/common.service';

@Injectable()
export class HostelService {

    private hostelServiceUrl = `${CommonProperties.serviceURL}api/hostels/`; 
    // preferred to use template literal rather concatination  like below line
    // CommonProperties.serviceURL + 'api/admins/';  // URL to web api 

    constructor(private http: Http, private commonService: CommonService) {}

    getHostel(): Observable<any> {
        return this.http.get(this.hostelServiceUrl)
                        .map((res: Response) => res.json());
    }
   
    getHostelByID(id) {
        return this.http.get(`${this.hostelServiceUrl}${id}`)
                .map((res: Response) => res.json());
    }
   
    saveHostel(objHostel) {
        const body = JSON.stringify(objHostel);
        return this.http.post(this.hostelServiceUrl, body, xhrHeaders())
            .map((res: Response) => res.json());
    }

    deleteHostel(id) {
        return this.http.delete(`${this.hostelServiceUrl}${id}`);
   }

  updateHostel(id, objHostel) {
      const body = JSON.stringify(objHostel);
      return this.http.put(`${this.hostelServiceUrl}${id}`, body, xhrHeaders()).map((res: Response) => res.json());
  }
  
}



