import { Injectable } from '@angular/core';

@Injectable()
export class PaginationService {

    // Pagination
  itemList: any;
  filteredItems: any[];
  pages: number = 4;
  pageSize: number = 10;
  pageNumber: number = 0;
  currentIndex: number = 1;
  items: any[];
  pagesIndex: number[];
  pageStart: number = 1;
  inputName: string = '';
  searchBy: string = '';

 // end

 // Pagination

 init() {
    // alert('dfdfdf');
    this.currentIndex = 1;
    this.pageStart = 1;
    this.pages = 4;
    this.pageNumber = parseInt('' + (this.filteredItems.length / this.pageSize));
    if (this.filteredItems.length % this.pageSize != 0) {
       this.pageNumber ++;
    }

    if (this.pageNumber  < this.pages) {
          this.pages =  this.pageNumber;
    }
  
    this.refreshItems();
   // console.log('this.pageNumber :  ' + this.pageNumber);
  // return this.items;
}

fillArray(): any {
 const obj = new Array();
 for (let index = this.pageStart; index < this.pageStart + this.pages; index ++) {
             obj.push(index);
 }
 return obj;
}
refreshItems() {
  this.items = this.filteredItems
                   .slice((this.currentIndex - 1) * this.pageSize, (this.currentIndex) * this.pageSize);
this.pagesIndex = this.fillArray();

}
prevPage() {
 if (this.currentIndex > 1) {
    this.currentIndex --;
 } 
 if (this.currentIndex < this.pageStart) {
    this.pageStart = this.currentIndex;
 }
 this.refreshItems();
}
nextPage() {
 if (this.currentIndex < this.pageNumber) {
       this.currentIndex ++;
 }
 if (this.currentIndex >= (this.pageStart + this.pages)) {
    this.pageStart = this.currentIndex - this.pages + 1;
 }

 this.refreshItems();
}
setPage(index: number) {
    this.currentIndex = index;
    this.refreshItems();
}

onPageSizeChange(value) {
 if (parseInt(value) === 0) {
      return false;
     } else {
 this.pageSize = value;
 this.init();
 }
}

onFilterChange(value) {
this.searchBy = value;
}

FilterByName(value) {
 this.filteredItems = [];
 if (value != '') {
       this.itemList.forEach(element => {
         if (JSON.stringify(element).toLowerCase().indexOf(value.toLowerCase()) >= 0 ) { // for any thing
       // if (JSON.stringify(element.FirstName).toLowerCase().indexOf(value.toLowerCase()) >= 0 ) {
         this.filteredItems.push(element);
  }
});
 }else {
    this.filteredItems = this.itemList;
 }
 this.init();
}

isDesc: boolean = false;
column: string = '';
// sorting 
sort(property) {
this.isDesc = !this.isDesc; // change the direction    
this.column = property;
let direction = this.isDesc ? 1 : -1;
this.items.sort(function(a, b) {
   if ( a[property] < b[property]) {
       return -1 * direction;
   } else if ( a[property] > b[property]) {
       return 1 * direction;
   } else {
       return 0;
   }
  });
}
// end for the Pagination


}



