import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { routing } from './hostel.routing';
import { HttpModule } from '@angular/http';
import { NgaModule } from '../../theme/nga.module';

import { HostelComponent } from './hostel.component';
import { HostelManageComponent } from './hostel-manage.component';
import { HostelService } from './hostel.service';

// import { SearchFilterPipe } from 'app/services/search-filter.pipe';
import { PaginationService } from '../../services/pagination.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    routing,
    NgaModule,
    HttpModule,
  ],
  declarations: [HostelComponent, HostelManageComponent],
  providers: [HostelService, PaginationService],
})

export class HostelModule {


 }



