import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HostelService } from './hostel.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/takeWhile';
import { CommonProperties } from '../common-properties';

import { CommonService } from '../../services/common.service';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

@Component({
  selector: 'hostel',
  templateUrl: './hostel.component.html',
  styleUrls: ['./hostel.component.css'],
})

export class HostelComponent implements OnInit, OnDestroy {

  isSubscribe: boolean = true;
  
  id: number;
  hostelSelected: any= {};
  hostelList: any;
  message: string;
  isErrorMessage: boolean= false;
  hostelname: string;
  objCurrentDate: Date;
 
  actives: any = [
    { id: 0, name: 'False' },
    { id: 1, name: 'True' },
  ];
  
  types: any = [
    { id: 'BOYS', name: 'BOYS' },
    { id: 'GIRLS', name: 'GIRLS' },
    { id: 'MIXED', name: 'MIXED' },
  ];

  faculties: any;
  classes: any;
  

 returnUrl = `hostel/managehostel/`;  
 selectedItem: any;

  constructor(private route: ActivatedRoute, private router: Router, private hostelService: HostelService, 
    private commonService: CommonService, private _http: Http, private commonProperties: CommonProperties) { }

  ngOnInit() {
    // console.log(JSON.parse(localStorage.getItem('currentUser')));
    this.objCurrentDate = new Date();
    this.route.params.subscribe(
      params => {
             this.id = +params['id']; // (+) converts string 'id' to a number
      });
   // get the Hostel to update
    if (this.id > 0) {
    this.getHostelByID(this.id);
    }
  }

  ngOnDestroy() {
    this.isSubscribe = false;
  }
 
// end for the same 

  // Get Hostel
  getHostel() {
          this.hostelService.getHostel().takeWhile(() => this.isSubscribe)
                          .subscribe(resData => this.hostelList = resData,
                            err => console.error(err),
                          // () => console.log(this.hostelList)
                          );
  }
                        
getHostelByID(id) {
            this.hostelService.getHostelByID(id).takeWhile(() => this.isSubscribe)
              .subscribe(resData => { 
                this.hostelSelected = resData; 
                this.hostelSelected.IsActive = parseInt(this.hostelSelected.IsActive);
              },
                err => console.error(err),
      );
                            
}

  HideMessage() {
    this.message = '';
    // return this.isErrorMessage = !this.isErrorMessage;
  }

  // Update Hostel
  updateHostel(id, objHostel) {
    objHostel.ID = id;
    objHostel.LastUpdatedBy = this.commonProperties.currentUser.ID; //JSON.parse(localStorage.getItem('currentUser')).ID;
    objHostel.LastModifiedOn = new Date(); 
   // objHostel.CreatedBy = this.hostelSelected.CreatedBy;
   // objHostel.CreatedOn = this.hostelSelected.CreatedOn;
    this.hostelService.updateHostel(id, objHostel).subscribe (
       data => {
         // refresh the list
        // this.getHostel();
         this.message = 'Hostel updated successfully! ';
        // this.router.navigate([this.returnUrl]);
         return true;
       },
       error => {
         console.error('Error saving Hostel!');
         return Observable.throw(error);
       },
    );

  }

  onBack() {
  this.router.navigate([this.returnUrl]);
}

 saveHostel (objHostel: any) {
  objHostel.CreatedBy = this.commonProperties.currentUser.ID; //JSON.parse(localStorage.getItem('currentUser')).ID;
  objHostel.CreatedOn = new Date(); 
  this.hostelService.saveHostel(objHostel).takeWhile(() => this.isSubscribe)
                    .subscribe(
                      resData => {
                        // this.getHostel();
                        this.isErrorMessage = false;
                        this.hostelname = '';
                        this.message = 'Data saved successfully! ';
                      },
                      error => {
                        this.isErrorMessage = true;
                        this.message = `Error ${error}`;
                        console.error('Error saving Hostel!');
                        return Observable.throw(error);
                  });
  }

  // Save Hostel Record
  submitHostel(value: any) {
  // console.log(value.value);
    if (!value.form.valid) {
      this.message = 'Please enter required values!';
      this.isErrorMessage = true;
      this.hostelname = 'danger';
        return;
    }
     
    // update the Hostel record
    if (this.id > 0) {
          this.updateHostel(this.id, value.value);  
          value.form.reset();
    } else {
          this.saveHostel(value.value);
          value.form.reset();
      }
  }

}



