import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { HostelService } from './hostel.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/takeWhile';

import { CommonProperties } from '../common-properties';

import { PaginationService } from '../../services/pagination.service';


@Component({
  selector: 'hostel-manage',
  templateUrl: './hostel-manage.component.html',
  // styleUrls: ['./smartTables.scss'],
   styleUrls: ['./hostel.component.css'],
  
})
export class HostelManageComponent implements OnInit, OnDestroy {

  isSubscribe: boolean = true;
 hostelServiceUrl: string;
  title = 'Manage Hostel';
  itemList: any;
  query: string = '';
  returnUrl: string;
  hostelID: number;

  constructor(private hostelService: HostelService, private route: ActivatedRoute, private router: Router, 
              private paginationService: PaginationService ) { }

  ngOnInit() {
    this.hostelServiceUrl = CommonProperties.serviceURL;
    this.getHostel();
  }

  ngOnDestroy() {
    this.isSubscribe = false;
  }

  // New Hostel
  newHostel() {
    this.router.navigate(['hostel/hostel/']);
  }

// Get Hostel
  getHostel() {
    $('#popup').fadeIn();
    $('#fade').fadeIn();
          this.hostelService.getHostel().takeWhile(() => this.isSubscribe)
                          .subscribe(
                            resData => {
                            this.paginationService.itemList = resData;
                            $('#popup').fadeOut();
                            $('#fade').fadeOut();
                            },
                            err => console.error(err),
                            () => { 
                              this.paginationService.filteredItems = this.paginationService.itemList;
                              this.paginationService.init();
                           },
                      );
  }

  // Update Hostel
  updateHostel(id) {
    this.returnUrl = `hostel/hostel/${id}`;  
    this.router.navigate([this.returnUrl]);
  }

  setHostelID(id): void {
    this.hostelID = id;
  }
  // Delete Hostel
  deleteHostel(): void {
      this.hostelService.deleteHostel(this.hostelID).takeWhile(() => this.isSubscribe).subscribe(
         data => {
           // refresh the list
           this.getHostel();
           return true;
         },
         error => {
           console.error('Error deleting Hostel!');
           return Observable.throw(error);
         },
      );
  }

}



